<template>
  <div class="active">
     <h2><i onclick="window.history.go(-1)"></i>{{data.title}}<router-link to="./"></router-link></h2>
    <div class="activebox">
      <Scheme :blocks='data.blocks'></Scheme>
    </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
  name: 'active',
  data () {
    return {
      msg: 'suolun',
      data:{
        'title':'',
        'backgroudColor':'white',
        'blocks':[]
      },
    }
  },
  created(){
    this.watchRouter();
  },
  watch:{
    '$route': 'watchRouter'
  },
  methods:{
    watchRouter(){
      let id = this.$route.query.id;
      this.getActiveData(id);
    },
    getActiveData(id){
      this.$http({
        method:'get',
        url:this.$api+'/h5/ads/ad_detail',
        params:{
          id:id
        }
      }).then(res=>{
        
        if(res.data.succ){
          let resData = JSON.parse(res.data.value.data);
          this.$set(this.data,'title',resData.title?resData.title:'年货节');
          this.$set(this.data,'backgroudColor',resData.backgroudColor?resData.backgroudColor:'white');
          this.$set(this.data,'blocks',resData.blocks?resData.blocks:[]);
        }
        this.$nextTick(()=>{
          document.getElementsByClassName('active')[0].style.background = this.data.backgroudColor;
        })

      }).catch(error=>{
          let instance = Toast('网络好像有问题,请稍候再试试');
        setTimeout(() => {
          instance.close();
        }, 2000);
        })
    }
  },
  mounted(){
    

    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
@import '../common/css/var.scss';
h2{
    height: 44px;
    line-height: 44px;
    position: fixed;
    top: 0;
    left:0;
    width: 100%;
    background: white;
    align-items:center;
    font-size:18px;
    color:#212121;
    font-family:PingFangSC-Regular;
    border-bottom: 1px solid #f4f4f4;/*no*/
    text-align: center;
    transform: translate3d(0,0,0);
    i{
      position: absolute;
      left:20px;
      top: 10px;
      width: 24px;
      height: 24px;
      background: url('../assets/order_navbar_arrow_back_icon@3x.png');
      background-size: 100%;
      margin-right: 10px;
    }
    a{
      position: absolute;
      top: 10px;
      right: 20px;
      width: 22px;
      height: 20px;
      background: url('../assets/back_home.png');
      background-size: 100%;
      cursor: pointer;
    }
  }
.active{
   max-width: 414px;
  margin: 0 auto;
  .activebox{
    padding:44px 0 48px;
    
      
  }
  

}
</style>



